	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/humans-txt/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://humanstxt.org/\" title=\"View the Humans.txt website.\">Humans.txt</a> is an interesting idea - it's (another) way for websites to create an association between the people (developers, authors, marketers, and so on) that are behind the creation of the website and the website itself, without actually including that information in the content of said website.  It's not exactly a web development standard, but it's a neat idea; and it's the kind of information that's \"there if you're looking for it\" but remains otherwise hidden from view - as it should be.</p>\n<p>The format itself is very simple - it's a plain-text file, called 'humans.txt', and it's placed at the root of the website.  In to that file you place whatever content you want, and whatever content is appropriate.  A little more elegant than placing that content in the comments of your HTML, as has been done in the past.</p>\n<p>For us Geeks, there's a neat little plugin available for Google Chrome, which simplifies the discovery and viewing of these humans.txt files, when they exist on any website you visit.</p>\n<p><a href=\"https://chrome.google.com/webstore/detail/pocdghmbbodjiclginddlaimdaholhfk\" title=\"View the humans.txt plugin for Chrome.\">The humans.txt plugin for Chrome is available from this link.</a></p>","frontmatter":{"title":"Humans TXT","date":"November 22, 2011","tags":null,"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/the-slides-from-my-presentation-at-flashmidlands/","tags":["actionscript","facebook","flash","presentations","the-flash-facebook-cookbook","flashmidlands"],"title":"The slides from my presentation at FlashMidlands.","date":"2011-11-20T20:50:42.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/javascript-testing-with-node-js-testacular-jasmine/","tags":["javascript","karma","nodejs","presentations","testacular"],"title":"Javascript Testing with Node.js, Testacular & Jasmine","date":"2013-05-06T11:43:08.000Z","description":null,"image":null}}}}

